.pulse {
  position: relative;
  font-size: 20px;
  background: rgb(46, 196, 223);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-animation-name: innercircle;
          animation-name: innercircle;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.pulse::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: rgb(46, 196, 223);
  opacity: 0.6;
  z-index: -1;
  height: 20px;
  width: 20px;
  transform: scale(1.5);
  border-radius: 50%;
  -webkit-animation: circle 3s linear infinite;
          animation: circle 3s linear infinite;
}
.pulse::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: rgb(46, 196, 223);
  z-index: -1;
  height: 20px;
  width: 20px;
  transform: scale(1.5);
  border-radius: 50%;
  -webkit-animation: outercircle;
          animation: outercircle;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes circle {
  0% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.4);
  }
  75% {
    opacity: 0;
    transform: scale(1.4);
  }
  100% {
    opacity: 0;
    transform: scale(1.4);
  }
}
@keyframes circle {
  0% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.4);
  }
  75% {
    opacity: 0;
    transform: scale(1.4);
  }
  100% {
    opacity: 0;
    transform: scale(1.4);
  }
}

@-webkit-keyframes innercircle {
  from {
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.1);
  }
  75% {
    transfrom: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}

@keyframes innercircle {
  from {
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.1);
  }
  75% {
    transfrom: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}
@-webkit-keyframes outercircle {
  from {
    opacity: 0.6;
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    opacity: 0.6;
    transform: scale(1.6);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.8);
  }
  75% {
    opacity: 0;
    transfrom: scale(2.4);
  }
  to {
    opacity: 0;
    transform: scale(2.4);
  }
}
@keyframes outercircle {
  from {
    opacity: 0.6;
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    opacity: 0.6;
    transform: scale(1.6);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.8);
  }
  75% {
    opacity: 0;
    transfrom: scale(2.4);
  }
  to {
    opacity: 0;
    transform: scale(2.4);
  }
}
