/*rotating cards*/

.card-testi,
.card-testi-1 {
  padding: 0px;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}
.rotating-cards,
.rotating-cards-1 {
  width: 100%;
  position: relative;
  perspective: 1000px;
  transform-style: preserve-3d;
}

.rotating-cards > div,
.rotating-cards-1 > div {
  width: 100%;
  height: 100px;

  transition: 0.5s;
  position: absolute;
  left: 0%;
  top: calc(50% - 4px);
}

.rotating-cards-1 > div.current {
  transform: translate3d(-0.5%, 0%, 50px);
  /*black*/
  color: #212529;
  /*color: #2ec4df;*/
}
.rotating-cards-1 > div.next {
  transform: translate3d(-5%, 70%, -50px);
  /*back */
  color: #21252955;
  /*color: #2ec4df55;*/
}

.rotating-cards > div.current {
  transform: translate3d(2%, -45%, -50px);
  color: #2ec4df;
}
.rotating-cards > div.prev {
  transform: translate3d(-2%, -110%, -150px);
  color: #2ec4df55;
}
.rotating-cards > div.next {
  transform: translate3d(-2%, 30%, -150px);
  color: #2ec4df55;
}
/*    .rotating-cards > div.back-prev{
        transform: translate3d(0%,-70%,-100px);
        color: #353235;
      }*/
.rotating-cards > div.back-next {
  transform: translate3d(-4%, 105%, -200px);
  color: #2ec4df22;
}

/* -------------------------animate text////------------------------- */
.animate {
  height: auto;
  /* background: #111; */
  background-size: 0.2em 100%;
  font: 5.5em/1 Open Sans, Impact;
  text-transform: uppercase;
  margin: 0;
}

.text {
  fill: none;
  stroke-width: 6;
  stroke-linejoin: round;
  stroke-dasharray: 70 330;
  stroke-dashoffset: 0;
  -webkit-animation: stroke 6s infinite linear;
  animation: stroke 6s infinite linear;
}

.text:nth-child(5n + 1) {
  stroke: #f2385a;
  -webkit-animation-delay: -1.2s;
  animation-delay: -1.2s;
}
.text:nth-child(5n + 2) {
  stroke: #f5a503;
  -webkit-animation-delay: -2.4s;
  animation-delay: -2.4s;
}

.text:nth-child(5n + 3) {
  /* stroke: #E9F1DF; */
  stroke: rgb(153, 204, 34);
  -webkit-animation-delay: -3.6s;
  animation-delay: -3.6s;
}

.text:nth-child(5n + 4) {
  stroke: #56d9cd;
  -webkit-animation-delay: -4.8s;
  animation-delay: -4.8s;
}

.text:nth-child(5n + 5) {
  stroke: #3aa1bf;
  -webkit-animation-delay: -6s;
  animation-delay: -6s;
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}
